import Link from 'next/link'
import Image from 'next/image'
import { useState } from 'react'

interface Listing {
  id: string
  title: string
  location: string
  price: number
  rating: number
  reviews: number
  images: string[]
  amenities: string[]
  description: string
  host: {
    name: string
    avatar: string
    superhost: boolean
  }
  maxGuests: number
  bedrooms: number
  bathrooms: number
}

interface ListingCardProps {
  listing: Listing
}

export default function ListingCard({ listing }: ListingCardProps) {
  const [imageError, setImageError] = useState(false)
  const [currentImageIndex, setCurrentImageIndex] = useState(0)

  const handleImageError = () => {
    setImageError(true)
  }

  const nextImage = (e: React.MouseEvent) => {
    e.preventDefault()
    e.stopPropagation()
    setCurrentImageIndex((prev) => 
      prev === listing.images.length - 1 ? 0 : prev + 1
    )
  }

  const prevImage = (e: React.MouseEvent) => {
    e.preventDefault()
    e.stopPropagation()
    setCurrentImageIndex((prev) => 
      prev === 0 ? listing.images.length - 1 : prev - 1
    )
  }

  const fallbackImage = 'https://images.pexels.com/photos/271618/pexels-photo-271618.jpeg'

  return (
    <Link href={`/listing/${listing.id}`} className="group block">
      <div className="bg-white rounded-lg shadow-sm border overflow-hidden hover:shadow-lg transition-all duration-300 group-hover:scale-[1.02]">
        {/* Image Section */}
        <div className="relative h-48 overflow-hidden">
          <Image
            src={imageError ? fallbackImage : listing.images[currentImageIndex]}
            alt={listing.title}
            fill
            className="object-cover group-hover:scale-110 transition-transform duration-300"
            onError={handleImageError}
          />
          
          {/* Image Navigation */}
          {listing.images.length > 1 && (
            <>
              <button
                onClick={prevImage}
                className="absolute left-2 top-1/2 transform -translate-y-1/2 bg-white bg-opacity-80 hover:bg-opacity-100 rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity duration-200"
              >
                <svg className="w-4 h-4 text-gray-700" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </button>
              <button
                onClick={nextImage}
                className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-white bg-opacity-80 hover:bg-opacity-100 rounded-full p-1 opacity-0 group-hover:opacity-100 transition-opacity duration-200"
              >
                <svg className="w-4 h-4 text-gray-700" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </button>
              
              {/* Image Dots */}
              <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 flex space-x-1">
                {listing.images.map((_, index) => (
                  <div
                    key={index}
                    className={`w-1.5 h-1.5 rounded-full ${
                      index === currentImageIndex ? 'bg-white' : 'bg-white bg-opacity-50'
                    }`}
                  />
                ))}
              </div>
            </>
          )}

          {/* Superhost Badge */}
          {listing.host.superhost && (
            <div className="absolute top-2 left-2 bg-white px-2 py-1 rounded-full text-xs font-semibold text-gray-900">
              Superhost
            </div>
          )}

          {/* Heart Icon */}
          <button
            onClick={(e) => {
              e.preventDefault()
              e.stopPropagation()
              // Add to favorites functionality
            }}
            className="absolute top-2 right-2 p-2 rounded-full bg-white bg-opacity-80 hover:bg-opacity-100 transition-all duration-200"
          >
            <svg className="w-4 h-4 text-gray-700 hover:text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
            </svg>
          </button>
        </div>

        {/* Content Section */}
        <div className="p-4">
          {/* Location and Rating */}
          <div className="flex justify-between items-start mb-2">
            <p className="text-sm text-gray-600 font-medium">{listing.location}</p>
            <div className="flex items-center space-x-1">
              <svg className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
              </svg>
              <span className="text-sm font-medium text-gray-900">{listing.rating}</span>
              <span className="text-sm text-gray-500">({listing.reviews})</span>
            </div>
          </div>

          {/* Title */}
          <h3 className="font-semibold text-gray-900 mb-2 line-clamp-2 group-hover:text-pink-600 transition-colors">
            {listing.title}
          </h3>

          {/* Property Details */}
          <div className="flex items-center space-x-3 text-sm text-gray-600 mb-2">
            <span>{listing.maxGuests} guests</span>
            <span>•</span>
            <span>{listing.bedrooms} bedrooms</span>
            <span>•</span>
            <span>{listing.bathrooms} bathrooms</span>
          </div>

          {/* Amenities */}
          <div className="flex flex-wrap gap-1 mb-3">
            {listing.amenities.slice(0, 3).map((amenity, index) => (
              <span
                key={index}
                className="text-xs bg-gray-100 text-gray-700 px-2 py-1 rounded-full"
              >
                {amenity}
              </span>
            ))}
            {listing.amenities.length > 3 && (
              <span className="text-xs text-gray-500">
                +{listing.amenities.length - 3} more
              </span>
            )}
          </div>

          {/* Price */}
          <div className="flex items-baseline justify-between">
            <div>
              <span className="text-lg font-bold text-gray-900">${listing.price}</span>
              <span className="text-sm text-gray-600"> / night</span>
            </div>
            <div className="text-right">
              <p className="text-xs text-gray-500">Host: {listing.host.name}</p>
            </div>
          </div>
        </div>
      </div>
    </Link>
  )
}
